.scrollTop) {\n          d.scrollTop -= i.clientHeight\n        }\n      }\n    },\n\n    /**\n     * Handle keydowns of the 'down' arrow key.\n     * Move the active item down and scroll it into view, if necessary.\n     */\n    down () {\n      if (!this.showDropdown) {\n        return\n      }\n      if (this.current < this.items.length - 1) {\n        this.current++\n        const d = this.$refs.dropdown\n        const i = d.children[this.current]\n        if (i.offsetTop + i.clientHeight > d.scrollTop + d.clientHeight) {\n          d.scrollTop += i.clientHeight\n        }\n      }\n    },\n\n    /**\n     * Handle esc key keydown events.\n     * @param {Event} e - The original event.\n     */\n    esc (e) {\n      if (this.showDropdown) {\n        e.stopPropagation()\n        this.showDropdown = false\n      }\n    }\n  }\n}\n</script>\n\n<style lang=\"css\">\n.typeahead {\n  display: inline-block;\n  position: relative;\n}\n\n.typeahead .dropdown-menu {\n  display: none;\n  position: absolute;\n  top: 100%;\n  left: 0;\n  min-width: 100%;\n  max-height: 12rem;\n  overflow-y: auto;\n  list-style: none;\n  margin: 0;\n  padding: 0;\n  z-index: 2000;\n}\n\n.typeahead.open .dropdown-menu {\n  display: block;\n}\n\n.typeahead .dropdown-menu > li {\n  width: 100%;\n}\n\n.typeahead .dropdown-menu > li.active {\n  color: #fff;\n  background-color: #aaa;\n}\n\n.typeahead .dropdown-menu > li > a {\n  display: inline-block;\n  width: 100%;\n  cursor: pointer;\n}\n</style>\n"],"sourceRoot":""}]);

